export default function initConfiguratorScript() {
    const configurator = document.querySelector('.configurator')
    const configuratorBack = configurator?.querySelector('.configurator__top-link')


    configurator?.addEventListener("click", (e) => {
        if (e.target.classList.contains("step__next")) {
            openFilter(e.target)
        }
    })

    function openFilter(item) {
        setTimeout(() => {
            let nextStep = item.closest('.step').nextElementSibling
            nextStep?.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            })
        }, 200)
    }

    configuratorBack?.addEventListener('click', (e) => {
        e.preventDefault()
        window.history.go(-1)
    })


}