
// PLUGINS======================================================================================
import Inputmask from "inputmask"
//import initNoUiSlider from "./js-modules/nouislider"
import {Fancybox} from "@fancyapps/ui"
// import LazyLoad from "vanilla-lazyload"

// MODULES======================================================================================
import "./js-modules/sliders.js"
import "./js-modules/select.js"


import Toast from "./js-modules/toast";
// import initAnimations from "./js-modules/animations"
import initShowmore from "./js-modules/showmore"
import initStartVideo from "./js-modules/startVideo"
import initMobilemenu from "./js-modules/mobilemenu"
// import initDropdowns from './js-modules/dropdowns'
import initTabs from "./js-modules/tabs"
import Popup from "./js-modules/popup"
import initCatMenu from "./js-modules/catalog-menu"
import initOrderDropdown from "./js-modules/orderDropdown"
//import initQuantityInput from "./js-modules/quantityInput"
import initBasketHide from "./js-modules/basketHide"
import initInputFile from "./js-modules/inputFile"
import initCookieInfo from "./js-modules/cookie"
import initTextareaMore from "./js-modules/textareaMore"
import initPersonalMobileMenu from "./js-modules/personalMobileMenu"
import initTableWrapper from "./js-modules/tableWrapper"
import initMobileFilter from "./js-modules/mobileFilter"
import initMobileAdvantages from "./js-modules/mobileAdvantages"
import initConfiguratorScript from "./js-modules/configuratorScript"


// init ========================================================================================
document.addEventListener("DOMContentLoaded", () => {
    //let lazyload = new LazyLoad()
    Fancybox.bind(".product__slider-main a", {
        Toolbar: false,
        dragToClose: false,
        Thumbs: false,
    })
    Fancybox.bind("[data-fancybox]", {
        Toolbar: false,
        dragToClose: false,
        Thumbs: false,
    })


    initTabs()
    initCatMenu()
    initOrderDropdown()
    //initQuantityInput()
    initShowmore()
    // initStartVideo()
    //initNoUiSlider()
    initBasketHide()
    initInputFile()
    initCookieInfo()
    initTextareaMore()
    initPersonalMobileMenu()
    initTableWrapper()
    initMobileFilter()
    initMobileAdvantages()
    initConfiguratorScript()

    window.toast = new Toast()
    initMobilemenu()
    // initAnimations()
    // initDropdowns()
    window.popup = new Popup('[data-popup]')

    let imDate = new Inputmask("99.99.9999")
    imDate.mask(document.querySelectorAll('input[data-mask-date]'))

    window.initMaskPhone = function initMaskPhone() {
        let im = new Inputmask("+7 (999) 999-99-99")
        im.mask(document.querySelectorAll('[data-mask-phone]'))
    }
    initMaskPhone()


})
