export default function inputFile() {
    let fileInputs = document.querySelector('.custom-input--file');
    if(fileInputs) {
        fileInputs.value = null
        fileInputs.addEventListener('change', (event) => {
            if((event.target.files[0].size / 1024 / 1024) < 30) {
                let fileName = event.target.files[0].name
                let filePlaceholder = event.target.parentNode.querySelector('.custom-input__placeholder')
    
                filePlaceholder.innerHTML = fileName
                filePlaceholder.classList.add('active')
            }
            else {
                fileInputs.value = null
                window.toast.error('     30 ')
            }
        });
    }
}