import SlimSelect from 'slim-select'

//    Slim Select https://codepen.io/brexston74/pen/BamrZzN

document.addEventListener("DOMContentLoaded", () => {
	window.initSlimSelect('.default-select__select:not(.color)')
	window.initSlimSelect('.custom-select__select')
	window.initSlimSelect('.catalog-select__select')
	
})


//  
const configSelect = {
    messages: {
        placeholder: '',
        searchText: '  ',
        searchPlaceholder: '',  
    }
}

window.initSlimSelect = (selectList) => {
    selectList = document.querySelectorAll(selectList)

    selectList.forEach((selectItem) => {
        const placeholder = selectItem.getAttribute('data-placeholder');
        const limit = selectItem.getAttribute('data-limit');
        const search = selectItem.hasAttribute('data-search');
        const multiple = selectItem.hasAttribute('multiple');
        const hideSelected = selectItem.hasAttribute('data-hide');
        const resetBtn = selectItem.hasAttribute('data-reset');
        
        new SlimSelect({
            select: selectItem,
            placeholder: placeholder || configSelect.messages.placeholder,
            limit: limit,
            closeOnSelect: !multiple,
            showSearch: search,
            searchFocus: false,
            searchHighlight: true,
            searchText: configSelect.messages.searchText,
            searchPlaceholder: configSelect.messages.searchPlaceholder,
            allowDeselectOption: true,
            hideSelectedOption: hideSelected, 
            showContent: 'down',
            beforeOpen: function () { 
               selectItem.parentNode.classList.toggle('active')

            },
            beforeClose: function () { 
                selectItem.parentNode.classList.toggle('active')
            },
        })
   
        if(resetBtn) {
            let resetBtn = document.createElement("div")
            resetBtn.classList.add("reset-btn")
            resetBtn.innerText = ''
            selectItem.parentNode.querySelector('.ss-content').appendChild(resetBtn)

            resetBtn.addEventListener("click", () => {
                resetAllOption(selectItem)
            })
        }
    })

    function resetAllOption(selectItem) {
        let selectedValues = selectItem.parentNode.querySelectorAll('.ss-value-delete')
        selectedValues.forEach((value) => {
            value.click()
        })
    }
}

window.initColorSelect = (colors, select) => {
    select = document.querySelector(select)
    let ArrayColors = []

    for (let key in colors) {
        const itemColor = {innerHTML: 
        `<div class="color">
            <div class="color__bg" style="background:${colors[key].color}"></div>
            <div class="color__title">${colors[key].name}</div>
        </div>`,
        text: `${key}`}

        ArrayColors.push(itemColor)
    }

    new SlimSelect({
        select: select,
        valuesUseText: false,
        data: ArrayColors,
        showSearch: false,
        showContent: 'down',
    })
}