export default function initShowmore() {
    const dropdowns = document.querySelectorAll("[data-showmore]")
    
    dropdowns.forEach((dropdown) => {
        const moreBtn = Array.from(dropdown.children)
        let isOpen = false
        moreBtn[1].addEventListener("click", (e) => {
            if(isOpen ) {
                e.target.parentNode?.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                })  
            }
            e.target.parentNode.classList.toggle('active')
            isOpen = !isOpen
        })
        

        if(moreBtn[0].scrollHeight < 75) {
            moreBtn[1].classList.add('hide')
        }
   
    })
}