export default function initBasketHide() {
    const basketMore = document.querySelector(".b-form__more")
    if(!basketMore) return
    basketMore.addEventListener('click', () => {
        basketMore.classList.toggle('active')
        basketMore.parentNode.querySelector('.b-form__list').classList.toggle('active')
        if(basketMore.classList.contains('active')) {
            setTimeout(()=> {
                basketMore.parentNode.querySelector('.b-form__list').classList.add('scrollbar')
            }, 700)
        }
        else {
            basketMore.parentNode.querySelector('.b-form__list').classList.remove('scrollbar')
        }
    })
}