/**
 * @param {querySelector} burger - burger selector 
 * @param {querySelector} menu - menu selector
 */
export default function initMobilemenu(burgerClass, menuClass) {
    const burger = document.querySelector(".header__burger")
    if (!burger) return

    const menu = document.querySelector(".header__mobilemenu")

    burger.addEventListener("click", () => {
        if (body.classList.contains("mobilemenu-opened")) {
            document.removeEventListener("click", outsideEvtListener)
        } else {
            setTimeout(() => {
                document.addEventListener("click", outsideEvtListener)
            })
        }
        burger.classList.toggle("opened")
        menu.querySelector('.mobilemenu').classList.toggle("opened")
        body.classList.toggle("mobilemenu-opened")
    })

    function outsideEvtListener(evt) {
        if (evt.target === menu || menu.contains(evt.target)) return
        burger.classList.toggle("opened")
        body.classList.toggle("mobilemenu-opened")
        menu.querySelector('.mobilemenu').classList.toggle("opened")
        document.removeEventListener("click", outsideEvtListener)
    }

    //   
    let catalogBtn = document.querySelector('.header__catalog')
    let catalogMenu = document.querySelector('.catalogmenu')
    let catalogMenuClose = document.querySelector('.header__catalogmenu--close')

    catalogBtn.addEventListener('click', function() {
        menu.querySelector('.mobilemenu').classList.toggle("opened")
        document.removeEventListener("click", outsideEvtListener)

        setTimeout(() => {
            catalogMenu.classList.toggle('opened')
            catalogMenuClose.classList.toggle('active')
        }, 400)
    })

    catalogMenuClose.addEventListener('click', function() {
        catalogMenu.classList.toggle('opened')
        burger.classList.toggle("opened")
        catalogMenuClose.classList.toggle('active')
        body.classList.toggle("mobilemenu-opened")
    })
}
