export default function initCatMenu() {

    const catalogBtn = document.querySelector('.header__menu-item--catalog span')
    catalogBtn.addEventListener('click', function() {
        window.scrollTo(0, 0)
        document.body.classList.toggle('open-menu')
    })


    const catalogMenuList = document.querySelectorAll('.cm-item__top')

    catalogMenuList.forEach(item => item.addEventListener('click', function () {
        if(!this.parentNode.classList.contains('active')) {
            catalogMenuList.forEach((item) => {
                item.parentNode.classList.remove('active')
                item.parentNode.classList.add('mask')
            })
        }
        else {
            catalogMenuList.forEach((item) => {
                item.parentNode.classList.remove('mask')
            })            
        }
        
        this.parentNode.classList.toggle('active')
    }))

}