export default function initMobileAdvantages() {
    const advantageItems = document.querySelectorAll('.a-item__title:not(.no-tooltip)')

    if(window.matchMedia("(max-width: 768px)").matches && advantageItems) {

        advantageItems.forEach(item => item.addEventListener('click', function () {
            item.parentNode.classList.add('active')
            item.closest('.product').classList.add('blackout')
            
            item.parentNode.querySelector('.a-item__tooltip-close').addEventListener('click', function() {
                closeAdvantage()
            })
        }))

        document.addEventListener("click", (ev) => {
            if (ev.target.classList.contains("blackout")) {
                closeAdvantage()
            }
        })


        function closeAdvantage() {
            document.querySelector('.product').classList.remove('blackout')
            document.querySelector('.a-item.active').classList.remove('active')
        }
    }
    
}