export default function initCookieInfo() {

    const cookie = document.querySelector('.cookie')
    if (!cookie || localStorage.getItem('showCookiePush')) return

    const cookieApply = cookie.querySelector('.cookie__btn')
    const cookieClose = cookie.querySelector('.cookie__close')

    setTimeout(() => {
        cookie.classList.add('active')
    }, 3000)

    cookieApply.addEventListener('click', function () {
        closeCookie()
    })
    cookieClose.addEventListener('click', function () {
        closeCookie()
    })

    function closeCookie() {
        cookie.classList.remove('active')
        localStorage.setItem('showCookiePush', false);
    }
} 