import Swiper, { Navigation, Pagination, EffectFade, Autoplay, Mousewheel, Lazy, Thumbs } from "swiper"

Swiper.use([Navigation, Pagination, EffectFade, Thumbs, Autoplay])

document.addEventListener("DOMContentLoaded", () => {
	initSliders()
})

function initSliders() {

	const bannerSlider = document.querySelector(".banner__slider .swiper")
	
	if(bannerSlider) {
		const bannerText = document.querySelector(".banner__text .swiper")
		const bannerTextSlider = new Swiper(bannerText, {
			slidesPerView: 1,
			speed: 1500,
			allowTouchMove: false,
			effect: "fade",
			fadeEffect: {
				crossFade: true
			},
		})

		new Swiper(bannerSlider, {
			slidesPerView: 1,
			speed: 2000,
			autoplay: {
				delay: 5000,
				disableOnInteraction: false,
			},
			effect: "fade",
			fadeEffect: {
				crossFade: true
			},
			thumbs: {
				swiper: bannerTextSlider
			},
			pagination: {
				el: ".banner .swiper-pagination",
				clickable: true,
			},
		})
	}


	const aboutSlider = document.querySelector(".about__slider .swiper")
	
	if(aboutSlider) {
		const aboutSliderImg = document.querySelector(".about__slider-img .swiper")

		const aboutImg = new Swiper(aboutSliderImg, {
			slidesPerView: 1,
			speed: 900,
			spaceBetween: 16,
			allowTouchMove: false,
			effect: "fade",
			fadeEffect: {
				crossFade: true
			},
			watchSlidesProgress: true,
		})

		const aboutText = new Swiper(aboutSlider, {
			// slidesPerView: 1,
			speed: 900,
			spaceBetween: 20,
			autoHeight: true,
			// allowTouchMove: false,
			// watchSlidesProgress: true,
			thumbs: {
				swiper: aboutImg
			},
			navigation: {
				prevEl: aboutSlider.closest('.about').querySelector(".swiper-nav__prev"),      
				nextEl: aboutSlider.closest('.about').querySelector(".swiper-nav__next"),
			},
			pagination: {
				el: aboutSlider.closest('.about').querySelector(".swiper-nav__pagination"),
				type: "fraction",
				renderFraction: function (currentClass, totalClass) {
					return '<span class="' + currentClass + '"></span>' + '/' + '<span class="' + totalClass + '"></span>'
				}
			},
			
		})

	}


	const productGalleryThumb = document.querySelector(".product__slider-thumb .swiper")
	
	if(productGalleryThumb) {
		const productGalleryMain = document.querySelector(".product__slider-main .swiper")
		const productGalleryPhoto = document.querySelector(".product-photo")
		const galleryThumbs = new Swiper(productGalleryThumb, {
			slidesPerView: 4,
			direction: 'vertical',
			spaceBetween: 16,
			navigation: {
				prevEl: productGalleryThumb.closest('.product__slider-thumb').querySelector(".swiper-nav__top"),      
				nextEl: productGalleryThumb.closest('.product__slider-thumb').querySelector(".swiper-nav__bottom"),
			},
		})

		new Swiper(productGalleryMain, {
			slidesPerView: 1,
			direction: 'vertical',
			speed: 1000,
			spaceBetween: 16,
			thumbs: {
				swiper: galleryThumbs
			},
			on: {
				afterInit: (galleryThumbs) => {
					productGalleryPhoto.innerText = galleryThumbs.slides.length
				}
			},
			breakpoints: {
				768: {
					direction: 'vertical',
				},
				0: {
					direction: 'horizontal',
					spaceBetween: 8
				}
			}
			
		})
	}


	
	const goodsSlider = document.querySelector(".p-goods__slider .swiper")
	if(goodsSlider) {
		new Swiper(goodsSlider, {
			slidesPerView: 4,
			spaceBetween: 16,
			navigation: {
				prevEl: goodsSlider.closest('.p-goods').querySelector(".swiper-nav__prev"),
				nextEl: goodsSlider.closest('.p-goods').querySelector(".swiper-nav__next"),
			},
			breakpoints: {
				1250: {
					sliderPerView: 4
				},
				1023: {
					slidesPerView: 3,				
				},
				768: {
					slidesPerView: 2.2,			
				},
				0: {
					slidesPerView: 1.2,					
				}
			}
		})
	}


	const typicalSlider = document.querySelector(".typical__slider .swiper")

	new Swiper(typicalSlider, {
		slidesPerView: 1.2,
		spaceBetween: 64,
		autoplay: {
			delay: 3000,
			disableOnInteraction: false,
		},
		breakpoints: {
			768: {
				slidesPerView: 1.2,
				spaceBetween: 64,
				pagination: {
					el: ".swiper-pagination",
					type: "progressbar",
				}
			},
			0: {
				slidesPerView: 1,
				pagination: {
					el: ".swiper-pagination",
					clickable: true,
					type: 'bullets',
					
				},
			}
		}
	})


	const catalogSlider = document.querySelector(".catalog__slider .swiper")
	if(catalogSlider) {
		new Swiper(catalogSlider, {
			slidesPerView: 1,
			autoplay: {
				delay: 5000,
				disableOnInteraction: false,
			},
			navigation: {
				prevEl: catalogSlider.closest('.catalog__slider').querySelector(".swiper-nav__prev"),
				nextEl: catalogSlider.closest('.catalog__slider').querySelector(".swiper-nav__next"),
			},
			breakpoints: {
				768: {
					slidesPerView: 1,
					effect: "fade",
					fadeEffect: {
						crossFade: true
					},
					spaceBetween: 16,
					loop: false
				},
				0: {
					slidesPerView: 1.1,
					spaceBetween: 10,
					loop: true
				}
			}
		})
	}
}
