export default function initPersonalMobileMenu() {
    const personalActiveLink = document.querySelector('.personal__menu-link.active')

    if(personalActiveLink && window.matchMedia("(max-width: 767px)").matches) {
        
        personalActiveLink.addEventListener('click', function() {
            event.preventDefault()
            personalActiveLink.classList.toggle('arrow')
            let allLinks = personalActiveLink.parentNode.querySelectorAll('.personal__menu-link:not(.active)')
            
            allLinks.forEach((link) => {
                link.classList.toggle('show')
            })
        })
    }
}