export default function initMobileFilter() {

    const filterBtn = document.querySelector('.c-btn')

    if(!filterBtn) return
	const footerHeight = document.querySelector('footer').offsetHeight

	const filterBlock = document.querySelector('.c-filter')
    const filterSearch = document.querySelector('.c-filter__item--search')
    const header = document.querySelector('header')

    window.onscroll = () => {
        let scrollTop = document.documentElement.scrollTop
		if(scrollTop > 100) {
			filterBtn.classList.add('visible')
			if ((window.innerHeight + window.pageYOffset) >= document.body.offsetHeight - footerHeight) {
				filterBtn.classList.remove('visible')
			}
		} else {
			filterBtn.classList.remove('visible')
		}

    }

    filterBtn.addEventListener('click', function() {
        filterBlock.classList.toggle('active')
        document.body.classList.toggle('popup-opened')
        header.classList.add('header--fixed')
        let btnClose = document.querySelector('.header__back')
        btnClose.addEventListener('click', closeMenu)
    })

    filterSearch.addEventListener('click',closeMenu)

    function closeMenu() {
        filterBlock.classList.remove('active')
        document.body.classList.remove('popup-opened')
        header.classList.remove('header--fixed')
    }

}
